import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';

// import * as braintree from "braintree";
// import * as braintree from "braintree-web";

//---services
import { BraintreeService } from '../../services/braintree.service';
import * as dropin from 'braintree-web-drop-in';

/**
 * Generated class for the PaymentMethodsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-payment-methods',
  templateUrl: 'payment-methods.html',
})
export class PaymentMethodsPage {
  userData: any;

  //- create dron in
  CreateErr: any;
  Instance: any;

  booleanCreateErr: boolean = false;
  booleanInstance: boolean = false;

  //-data
  UserActual: any;

  //--timer
  objNodeTimer: any;
  segundos: number = 5;
  constructor(
    public alertCtrl: AlertController,
    public navCtrl: NavController, public navParams: NavParams,
    private braintreeService: BraintreeService,
    public loadingCtrl: LoadingController,
  ) {
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad PaymentMethodsPage');

    // this.userData = this.navParams.get('datos');
    // console.log(this.userData);
    // console.log(this.navParams.get('datos'));
    // this.showAlert();
    //this.newGateWay();
    // this.setTokenClient();
    this.UserActual = localStorage.getItem('verificacion');
    console.log(this.UserActual);
    this.crearCustomer();
    this.presentLoading();
  }

  goPayInfo() {
    if (this.booleanCreateErr) {
      this.showAlertPay();
    }
    if (this.booleanInstance) {
      this.startTimer();
      this.Instance.requestPaymentMethod(function (requestPaymentMethodErr, payload) {
        if (requestPaymentMethodErr) {
          console.log(requestPaymentMethodErr);
        }
        if (payload) {
          console.log(payload);
        }
      });
      // console.log(this.userData);
      // let Data = {'datos':this.userData};
      // this.navCtrl.push(PayinfoPage,Data);

    }

  }

  crearCustomer() {
    this.braintreeService.CrearCustomer(this.UserActual).then(
      (value) => {
        console.log('valueo');
        console.log(value);
        this.CallToken();
      }
    ).catch(
      (error) => {
        console.log('error');
        console.log(error);
      }
      );;
  }

  dropin(token) {
    // var button = document.querySelector('#submit-button');
    dropin.create({
      authorization: token,
      // authorization: '',
      //-- token inventado para probar que DropIn Ui no permite cualquier clave authorization: '',
      container: '#dropin-container',
      paypal: {
        flow: 'vault'
      }
    }, (createErr, instance) => {
      if (createErr) {
        console.log(createErr);
        this.CreateErr = createErr; this.booleanCreateErr = true;
      }
      if (instance) {
        console.log(instance);
        this.Instance = instance; this.booleanInstance = true;
      }

    });

  }
  CallToken() {
    // let token ='sandbox_gmhv57wq_xg698xhxgkr3z5wx';
    // let token:any = undefined;
    this.braintreeService.CrearTokenCustomer(this.UserActual).then(
      (value) => {
        console.log(value);
        this.dropin(value);
      }
    ).catch(
      (error) => {
        console.log(error);
      }
      );
  }
  //-alert  
  showAlertPay() {
    let alert = this.alertCtrl.create({
      title: 'Information',
      subTitle: 'verify the payment method information',
      buttons: ['OK']
    });
    alert.present();
  }

  //--- timer
  startTimer() {
    this.objNodeTimer = setInterval(() => this.timer(), 1000);
  }

  private timer() {
    if (this.segundos == 1) {
      clearInterval(this.objNodeTimer);
      this.goHome();
    } else {
      if (--this.segundos < 0) { }
    }
    console.log(this.segundos);
  }

  goHome() {
    // this.navCtrl.setRoot('ShowPage');
    this.navCtrl.push('ProviderOkPage');
  }

  presentLoading() {
    let loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 5000
    });
    loader.present();
  }
}



